.img-container {
  text-align: center;
  width: 50%;

}

img {
  width: 250px;
  height: 250px;
  padding-right: 10px;
  padding-left: 10px;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

img:hover {
  -webkit-transform: scale(1.06);
      -ms-transform: scale(1.06);
          transform: scale(1.06);
}




